import React from 'react';
import ReactDOM from 'react-dom';
import './slideshow.css';

export default class Slideshow extends React.Component {
    constructor(props) {
        super(props);

        /*
        * State slideIndex dng  xc nh xem slide no ang c active.
        * Cc nh s c xp chng ln nhau, ci no active th hin th,
        * Ci no khng active th n i.
        */
        this.state = {
            slideIndex: 0
        };

        /*
        * Khi s dng, mnh s truyn thuc tnh ratio, gi s l "3:2"
        * Nh vy, t l width/height l this.ratioWH = 3 / 2
        * Mnh s iu chnh cc nh sao cho v cng 1 kch thc.
        */
        const ratioWHArray = this.props.ratio.split(":");
        this.ratioWH = ratioWHArray[0] / ratioWHArray[1];

        this.backward = this.backward.bind(this);
        this.forward = this.forward.bind(this);
        this.setSlideIndex = this.setSlideIndex.bind(this);
        this.getNewSlideIndex = this.getNewSlideIndex.bind(this);
        this.updateDimensions = this.updateDimensions.bind(this);
        this.runAutomatic = this.runAutomatic.bind(this);
    }

    /*
    * Tnh ch s SlideIndex mi, vi step l bc nhy: +1 hoc -1
    * Gi s, mnh c 6 nh => cc ch s ln lt l: 0, 1, 2,...5
    * Ch :
    * Nu ang  ch s 5 m i v pha trc th slideIndex = 0
    * Nu ang  ch s 0 m v sau th slideIndex = 5
    */
    getNewSlideIndex(step) {
        const slideIndex = this.state.slideIndex;
        const numberSlide = this.props.input.length;

        let newSlideIndex = slideIndex + step;

        if (newSlideIndex >= numberSlide) newSlideIndex = 0;
        else if (newSlideIndex < 0) newSlideIndex = numberSlide - 1;

        return newSlideIndex;
    }

    // Quay v nh pha trc, tc index gim 1 => step = -1
    backward() {
        this.setState({
            slideIndex: this.getNewSlideIndex(-1)
        });
    }

    // Tin ti nh pha sau, tc index tng 1 => step = 1
    forward() {
        this.setState({
            slideIndex: this.getNewSlideIndex(1)
        });
    }

    // Xc nh slideIndex no s c active
    setSlideIndex(index) {
        this.setState({
            slideIndex: index
        })
    }

    /*
    * Cp nht chiu cao cho container khi thay i kch thc mn hnh
    * Trong phn CSS, mnh  container c width = 100%,
    * Nn d ko to hay thu nh th width vn bng 100%
    * Nhng chiu cao phi tnh li da vo width v t l this.ratioWH
    */
    updateDimensions() {
        this.containerElm.style.height
            = `${this.containerElm.offsetWidth / this.ratioWH}px`;
    }

    /*
    * Nu ngi dng truyn vo thuc tnh mode = automatic
    * th Slideshow s chy t ng da vo interval.
    * Hm ny s c gi trong mi lt lp li
    *  cp nht slideIndex mi cho Slideshow
    */
    runAutomatic() {
        this.setState({
            slideIndex: this.getNewSlideIndex(1)
        });
    }

    /*
    * Hm ny thuc v React Component Lifecycle
    * c gi sau khi component ny c render xong
    * Trong y, s lu li reference cho container - this.containerElm
    * Cp nht li chiu cao cho container - this.updateDimensions();
    * ng k s kin thay i kch thc - resize
    * Kim tra nu mode === "automatic" th s to mi mt interval
    *  thay i hnh nh - this.runAutomatic()
    * vi gi tr timeout c truyn t props hoc mc nh l 5000 ms
    */
    componentDidMount() {
        this.rootElm = ReactDOM.findDOMNode(this);
        this.containerElm = this.rootElm.querySelector(".container");

        this.updateDimensions();
        window.addEventListener("resize", this.updateDimensions);

        if (this.props.mode === "automatic") {
            const timeout = this.props.timeout || 5000;

            this.automaticInterval = setInterval(
                () => this.runAutomatic(),
                Number.parseInt(timeout)
            );
        }
    }

    /*
    * Hm ny cng thuc v React Component Lifecycle -
    * c gi khi component ny b xa khi mn hnh
    * Lc ny, mnh phi hy b s kin khi resize
    * v xa b interval  khai bo bn trn  trnh leak memory.
    */
    componentWillUnmount() {
        window.removeEventListener("resize", this.updateDimensions);
        if (this.automaticInterval) clearInterval(this.automaticInterval);
    }

    /*
    * Giao din ca component
    * vi phn t ngoi cng mnh  className="lp-slideshow"
    *  trnh xung t v tn vi cc component khc
    */
    render() {
        return (
            <div className="lp-slideshow">
                <div className="container">
                    {
                        this.props.input.map((image, index) => {
                            return (
                                <div
                                    key={index}
                                    className={
                                        `slide ${this.state.slideIndex === index ? "active" : ""}`
                                    }
                                >
                                    <div className="number-text">
                                        {`${index + 1} / ${this.props.input.length}`}
                                    </div>
                                    <img className="image" src={image.src} alt={image.caption} />
                                    <div className="caption-text">{image.caption}</div>
                                </div>
                            )
                        })
                    }

                    <span className="prev" onClick={this.backward}></span>
                    <span className="next" onClick={this.forward}></span>
                </div>

                <div className="dot-container">
                    {
                        this.props.input.map((_, index) => {
                            return (
                                <span
                                    key={index}
                                    className={
                                        `dot ${this.state.slideIndex === index ? "active" : ""}`
                                    }
                                    onClick={() => this.setSlideIndex(index)}
                                >
                                </span>
                            )
                        })
                    }
                </div>
            </div>
        );
    }
}


